<template>
  <div class="map-container">
    <div
      ref="map"
      class="map"
    ></div>
    <ul class="topMenu">
      <li class="tips"><i class="iconfont">&#xe65b;</i>{{this.$store.state.mapArea.name}}</li>
      <li
        v-if="!(this.$store.state.mapArea.name === this.$store.state.area)"
        class="tips goBack"
        @click="goBack"
      >
        <i class="iconfont">&#xe65b;</i>返回上一级
      </li>
    </ul>
  </div>
</template>
<script>
import http from '@/common/axios/http'
import api from '@/common/axios/api'
export default {
  name: 'Map',
  props: {
    'tagInfoData': {
      type: Object,
      default () {
        return {}
      }
    },
    'warnPercent': {
      type: Object,
      default () {
        return {
          attend: '30%',
          temperature: '30%'
        }
      }
    }
  },
  data () {
    const $this = this
    return {
      option: {
        tooltip: {
          trigger: 'item',
          backgroundColor: 'transparent',
          formatter (params) {
            if (params.seriesName !== '体温报警区域') {
              let result = $this.tagInfoData.data.filter((p) => {
                return p.cityName === params.name || p.districtName === params.name || p.gardenName === params.name || p.name === params.name
              })
              if (result.length) {
                const res = result[0]
                let html = '<div class="tag-box">'
                html += '<h3><i class="iconfont">&#xe79a;</i>' + params.name + '</h3>'
                html += '<ul>'
                switch ($this.tagInfoData.name) {
                  case '健康安全':
                    // html += '<li>出勤率<i>' + Math.round((res.attendCount / res.totalCount) * 100).toFixed(2) + '%</i></li>'
                    html += '<li>出勤人数<i>' + res.attendCount + '人</i></li>'
                    html += '<li>体温异常人数<i>' + res.warnCount + '人</i></li>'
                    html += '<li>需要服药人数<i>' + res.fyCount + '人</i></li>'
                    html += '<li>需要加餐人数<i>' + res.jcCount + '人</i></li>'
                    html += '<li>需要观察人数<i>' + res.gcCount + '人</i></li>'
                    if (res.leaveCount / res.totalCount * 100 >= parseFloat($this.warnPercent.attend) || (res.gcCount + res.fyCount) / res.totalCount * 100 >= parseFloat($this.warnPercent.temperature)) {
                      html += '<li class="warn">请假预警<i>' + (res.leaveCount / res.totalCount * 100).toFixed(2) + '%</i></li>'
                      html += '<li class="warn">疾病预警<i>' + ((res.gcCount + res.fyCount) / res.totalCount * 100).toFixed(2) + '%</i></li>'
                    } else {
                      html += '<li style="color:#62ff15">请假预警<i>暂无预警</i></li>'
                      html += '<li style="color:#62ff15">疾病预警<i>暂无预警</i></li>'
                    }
                    break
                  case '身体发育':
                    const forEslint = [res.data['偏轻'], res.data['均值'], res.data['肥胖'], res.data['偏重'], res.data['超重']]
                    html += '<li>BMI偏轻<i>' + forEslint[0] + '人</i></li>'
                    html += '<li>BMI均值<i>' + forEslint[1] + '人</i></li>'
                    html += '<li>BMI肥胖<i>' + forEslint[2] + '人</i></li>'
                    html += '<li>BMI偏重<i>' + forEslint[3] + '人</i></li>'
                    html += '<li>BMI超重<i>' + forEslint[4] + '人</i></li>'
                    break
                  case '睡眠/体育活动':
                    html += '<li>强度低<i>' + res.percentLowery + '%</i></li>'
                    html += '<li>强度中<i>' + res.percentCenter + '%</i></li>'
                    html += '<li>强度高<i>' + res.percentHeavy + '%</i></li>'
                    html += '<li>基本没睡<i>' + res.percentNoSleep + '%</i></li>'
                    html += '<li>睡眠较少<i>' + res.percentLessSleep + '%</i></li>'
                    html += '<li>睡眠正常<i>' + res.percentNormalSleep + '%</i></li>'
                    html += '<li>睡眠正常<i>' + res.percentMoreSleep + '%</i></li>'
                    break
                }
                html += '</ul>'
                html += '</div>'
                return html
              } else {
                return '<div class="tag-box"><h3><i class="iconfont">&#xe60d;</i>' + params.name + '</h3><p style="color:#ffe98f">暂无数据</p></div>'
              }
            }
          }
        },
        legend: {
          data: ['体温报警区域', '所有园所分布'],
          orient: 'vertical',
          left: '20',
          bottom: '20',
          textStyle: {
            color: '#4dd5ff'
          }
        },
        animationDuration: 1000,
        animationEasing: 'cubicOut',
        animationDurationUpdate: 1000
      },
      mapChart: {},
      // 省级拼音列表
      provinces: {},
      // 市级编号列表
      cityMap: {},
      countyMapData: {},
      // 首次登陆记录信息
      initData: {
        isOnce: true
      },
      // 各园所经纬度列表
      schoolCoor: {},
      // 报红园所列表
      feverData: [],
      goTopMap: false,
      centerPointers: [],
      gardenPointers: []
    }
  },
  watch: {
    'tagInfoData': {
      deep: true,
      handler: function (newVal) {
        if (newVal.name === '健康安全') {
          this.tagInfoRefresh()
        }
      }
    }
  },
  methods: {
    tagInfoRefresh () {
      // 报警
      const attend = this.tagInfoData.data.filter((p) => {
        return p.leaveCount / p.totalCount * 100 >= parseFloat(this.warnPercent.attend) || p.warnTempCount / p.totalCount * 100 >= parseFloat(this.warnPercent.temperature)
      })
      let arr = []
      let pointers = []
      this.$store.state.mapArea.level !== '县级' ? pointers = this.centerPointers : pointers = this.gardenPointers
      for (let i = 0, len = attend.length; i < len; i++) {
        const result = pointers.filter((p) => {
          return p.name === attend[i].cityName || p.name === attend[i].districtName
        })
        arr = arr.concat(result)
      }
      this.option.series[1].data = arr
      this.mapChart.setOption(this.option)
    },
    // 返回上级地图
    goBack () {
      const crumbs = this.$store.state.crumbs
      const level = this.$store.state.mapArea.level
      switch (level) {
        case '省级':

          break
        case '市级':
          const currArea = crumbs.province.substring(0, crumbs.province.length - 1)
          this.provincialMap(currArea)
          break
        case '县级':
          this.municipalMap(crumbs.city)
          break
      }
    },
    mapEchartsInit () {
      // 初始化市级列表
      this.$http.get('/static/map/cityMap.json').then((res) => {
        this.cityMap = res.data
      })
      // 初始化县级列表
      this.$http.get('/static/map/regionalism/guangxi.json').then((res) => {
        this.countyMapData = res.data
      })
      // 初始化所有园所经纬度列表
      // this.schoolCoor = {'富阳市龙门镇中心幼儿园': [119.846692, 30.001094], '淳安县蓓蕾幼儿园': [119.055495, 29.598947]}
      // 初始化所有报红园所列表
      // this.feverData = [{name: '富阳市龙门镇中心幼儿园', value: 1}, {name: '淳安县蓓蕾幼儿园', value: 2}]
      // 绘制地图
      this.mapChart = this.$echarts.init(this.$refs.map)
      // 获取地区id列表
      window.$.ajax({
        url: 'http://api.youjiaoyun.net/weixin.asmx/GetCityList?jsoncallback=?',
        dataType: 'jsonp',
        data: {ProvinceId: 20}, // 陕西 27, 广西 20, 浙江 11  青秀区 1869
        jsonp: 'jsoncallback',
        async: false,
        success: (result) => {
          this.$store.dispatch('changeAreaIdList', result)
          // 获取省级列表
          this.$http.get('/static/map/provinces.json').then((res) => {
            this.provinces = res.data
            if (this.$store.state.mapArea.level === '园所级') {
              this.$store.dispatch('changeMapArea', this.$store.state.crumbs.district)
            } else {
              this.$store.dispatch('changeMapArea', this.$store.state.area)
            }
            const currProvinceStr = this.$store.state.mapArea.name
            const currArea = currProvinceStr.substring(0, currProvinceStr.length - 1)
            if (this.provinces[currArea]) {
              // 绘制省级地图
              this.provincialMap(currArea)
            } else if (this.cityMap[currProvinceStr]) {
              // 绘制市级地图
              this.municipalMap(currProvinceStr)
            } else {
              // 绘制县级地图
              this.countyMap(currProvinceStr)
            }
            // 地图点击事件
            this.mapChart.on('click', this.handleMapClick)
          })
        },
        error: function (err) {
          console.log(err)
        }
      })
    },
    // 绘制省级地图
    provincialMap (currArea) {
      const curr = currArea + '省'
      // 切换到当前城市
      this.$store.dispatch('changeMapArea', curr)
      this.$http.get('/static/map/province/' + this.provinces[currArea] + '.json').then((res) => {
        const d = []
        const arr = []
        const data = res.data
        for (let i in data.features) {
          d.push({
            name: data.features[i].properties.name
          })
          arr.push({
            name: data.features[i].properties.name,
            value: data.features[i].properties.cp
          })
        }
        this.centerPointers = arr
        this.$store.dispatch('changeMapLevel', '省级')
        // 绘制地图
        this.drawMap(d, data)
      })
      this.$emit('mapObiterData', {name: curr, level: '省级'})
      // 幼儿园，教师信息
      this.overview('provinceName', curr)
      // 记录当前区域等级
      // 记录面包屑
      this.$store.dispatch('crumbsProvince', curr)
    },
    // 绘制市级地图
    municipalMap (currArea) {
      // 切换到当前城市
      this.$store.dispatch('changeMapArea', currArea)
      this.$http.get('/static/map/city/' + this.cityMap[currArea] + '.json').then((res) => {
        const d = []
        const arr = []
        const data = res.data
        this.$echarts.registerMap(currArea, data)
        for (let i in data.features) {
          d.push({
            name: data.features[i].properties.name
          })
          arr.push({
            name: data.features[i].properties.name,
            value: data.features[i].centerPoint
          })
        }
        this.centerPointers = arr
        this.$store.dispatch('changeMapLevel', '市级')
        // 绘制地图
        this.drawMap(d, data)
      })
      this.$emit('mapObiterData', {name: currArea, level: '市级'})
      // 幼儿园，教师信息
      this.overview('cityName', currArea)
      this.$store.dispatch('crumbsCity', currArea)
    },
    // 绘制县级地图
    countyMap (currArea) {
      // 切换到当前城市
      this.$store.dispatch('changeMapArea', currArea)
      this.gardenList(currArea)
    },
    // 当前区域总览数据（幼儿园，教师信息）
    overview: async function (paramsId, areaName) {
      let res
      switch (paramsId) {
        case 'provinceName':
          res = await http.get(api.generalOverview, {
            provinceName: areaName
          })
          break
        case 'cityName':
          res = await http.get(api.generalOverview, {
            cityName: areaName
          })
          break
        case 'districtName':
          res = await http.get(api.generalOverview, {
            districtName: areaName
          })
          break
        case 'gardenId':
          res = await http.get(api.generalOverview, {
            gardenId: areaName
          })
      }
      if (res.data) {
        const d = res.data.data
        console.log('当前区域总览数据（幼儿园，教师信息）')
        console.log(d)
        this.$emit('generalData', d)
      }
    },
    // 搜索隶属城市名
    findSubjection (currArea) {
      const countyData = this.countyMapData
      let d = ''
      for (let i in countyData.city) {
        countyData.city[i].area.forEach((val, index) => {
          if (val === currArea) {
            d = countyData.city[i].name + '市'
          }
        })
      }
      return d
    },
    drawMap (d, data) {
      const arr = []
      if (this.$store.state.mapArea.level !== '县级') {
        for (let i in d) {
          arr.push(d[i].name)
        }
        this.$store.dispatch('changeMapList', arr)
      }
      this.$store.dispatch('changeAreaList', this.$store.state.mapArea.list)
      // 注册地图
      this.$echarts.registerMap(this.$store.state.mapArea.name, data)
      // 绘制地图
      this.renderMap(this.$store.state.mapArea.name, d)
      // 记录首次登陆数据
      if (this.initData.isOnce) {
        Object.defineProperty(this.initData, 'city', {
          value: this.$store.state.mapArea.name,
          writable: false,
          enumerable: true,
          configurable: true
        })
        this.$store.dispatch('changeAreaLevel', this.$store.state.mapArea.level)
        this.initData.isOnce = !this.initData.isOnce
      }
    },
    // 当前县所有园区
    gardenList: async function (currArea) {
      const res = await http.get(api.resolvegarden, {
        districtName: currArea
      })
      if (res.data) {
        const r = res.data.data
        let result = []
        const gardenPointersResult = []
        if (r.length) {
          for (let i = 0, len = r.length; i < len; i++) {
            result.push(r[i].gardenName)
            gardenPointersResult.push({
              name: r[i].gardenName,
              value: [r[i].longitude, r[i].latitude]
            })
          }
        } else {
          result = ['暂无']
        }
        this.gardenPointers = gardenPointersResult
        this.$store.dispatch('changeMapList', result)
        this.$emit('mapObiterData', {name: currArea, level: '县级'})
        const findSubjection = this.findSubjection(currArea)
        this.$http.get('/static/map/city/' + this.cityMap[findSubjection] + '.json').then((res) => {
          const d = currArea
          // 筛选出当前县数据
          const currData = res.data.features.filter((p) => {
            return p.properties.name === currArea
          })
          const data = {
            'type': 'FeatureCollection',
            'features': currData
          }
          this.$echarts.registerMap(currArea, data)
          this.centerPointers = {
            name: currArea,
            value: currData[0].centerPoint
          }
          this.$store.dispatch('changeMapLevel', '县级')
          // 绘制地图
          this.drawMap(d, data)
        })
        // 幼儿园，教师信息
        this.overview('districtName', currArea)
        this.$store.dispatch('crumbsDistrict', currArea)
      }
    },
    handleMapClick (params) {
      localStorage.setItem('areaLevel', '省级')
      // 判断是否市级地图
      if (this.cityMap[params.name]) {
        localStorage.setItem('areaLevel', '市级')
        this.municipalMap(params.name)
      // 判断是否县级地图
      }
      if (this.findSubjection(params.name)) {
        localStorage.setItem('areaLevel', '县级')
        this.countyMap(params.name)
      }
      if (params.seriesName === '所有园所分布') {
        localStorage.setItem('areaLevel', '园所级')
        this.$store.dispatch('changeMapLevel', '园所级')
        this.$store.dispatch('crumbsGarden', params.name)
        // 缓存园所对应所属id列表
        const crumbs = this.$store.state.crumbs
        const gardenIdList = {
          province: this.getAreaId(crumbs.province),
          city: this.getAreaId(crumbs.city),
          district: this.getAreaId(crumbs.district),
          garden: this.getGardenId(crumbs.garden),
        }
        console.log(gardenIdList)
        localStorage.setItem('provinceId', gardenIdList.province[0].ProvinceId)
        localStorage.setItem('cityId', gardenIdList.city[0].CityId)
        localStorage.setItem('districtId', gardenIdList.district[0].DistrictId)
        localStorage.setItem('gardenId', gardenIdList.garden)
        localStorage.setItem('gardenName', crumbs.garden)
        // 切换到园所视角
        this.$router.push({path: '/garden' + this.$route.path})
      }
    },
    // 获取该地区id
    getAreaId (name) {
      return this.$store.state.areaIdList.filter((item) => {
        return item.ProvinceName === name || item.CityName === name || item.DistrictName === name
      })
    },
    // 获取该园所id
    getGardenId (name) {
      console.log(this.$store.state.gardenIdList, 1)
      const gardenId = this.$store.state.gardenIdList.filter((item) => {
        return item.gardenName === name
      })
      return gardenId[0].gardenId
    },
    renderMap (map, data) {
      this.option.geo = {
        map: map,
        roam: false,
        nameMap: {
          'china': '中国'
        },
        label: {
          normal: {
            show: true,
            textStyle: {
              color: '#fff',
              fontSize: 13
            }
          },
          emphasis: {
            show: true,
            textStyle: {
              color: '#fff',
              fontSize: 13
            }
          }
        },
        itemStyle: {
          normal: {
            areaColor: '#1683fb',
            borderColor: '#47e3ff',
            borderType: 'solid',
            borderWidth: 0.4
          },
          emphasis: {
            areaColor: '#1da8fc'
          }
        }
      }
      // const areaColor = ['#1da8fc','#7189ff', '#1683fb', '#57d0ff', '#2dc1fc', '#1683fb', '#2dc1fc', '#2dcfea', '#7189ff', '#8ddcf7', '#1da8fc', '#1da8fc', '#2dc1fc', '#3ecce4']
      const areaColor = ['#2388f8', '#177ef0', '#2387f4', '#3690f3', '#4b9df6', '#318ff6', '#1e88fd', '#1480f6', '#107cf2', '#2186f4', '#1a81f1', '#0f76e8', '#1f80ea', '#1276e4']
      const areaColorArr = []
      for (let i = 0, len = this.$store.state.mapArea.list.length; i < len; i++) {
        areaColorArr.push({
          name: this.$store.state.mapArea.list[i],
          itemStyle: {
            areaColor: areaColor[i]
          }
        })
      }
      this.option.geo.regions = areaColorArr
      if (this.$store.state.mapArea.level !== '县级') {
        this.option.series = [{
          name: '所有中心区域',
          type: 'effectScatter',
          coordinateSystem: 'geo',
          data: this.centerPointers,
          symbolSize: 8,
          symbol: '',
          color: '#0060ca',
          showEffectOn: 'render',
          rippleEffect: {
            brushType: 'stroke'
          },
          hoverAnimation: true
        }]
      } else {
        this.option.series = [{
          name: '所有园所分布',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: this.gardenPointers,
          symbolSize: 18,
          symbol: 'path://M849.6 812V486.2l-300-150V137L174.6 324.6V812H137v75h750V812h-37.4z m-450-37.4h-75v-75h75v75z m0-150h-75v-75h75v75z m0-150h-75v-75h75v75z m300 300h-75v-75h75v75z m0-150h-75v-75h75v75z',
          color: '#92f5ff'
        }]
      }
      if (this.$route.name === 'HealthyToday') {
        this.option.series.push({
          name: '体温报警区域',
          type: 'effectScatter',
          coordinateSystem: 'geo',
          silent: true,
          zlevel: 9,
          data: [],
          symbolSize: 18,
          symbol: 'path://m291.2229,189.35269l-5.03619,-8.72086c-1.51839,-2.62844 -4.00193,-6.93142 -5.52033,-9.56037l-5.03572,-8.72033c-1.5199,-2.62999 -4.00344,-2.62999 -5.52133,0l-5.0347,8.72033c-1.51839,2.62895 -4.00345,6.93293 -5.52134,9.56037l-5.03521,8.72086c-1.51839,2.63098 -0.2756,4.78145 2.76067,4.78145l31.18046,0c3.03931,0 4.28056,-2.15047 2.76368,-4.78145l0,0zm-16.91486,1.35006l-2.98695,0l0,-2.86743l2.98695,0l0,2.86743zm-0.1439,-4.98027l-2.69659,0l-0.26799,-12.26868l3.20865,0l-0.24407,12.26868l0,0z',
          color: '#ffe98f',
          showEffectOn: 'render',
          rippleEffect: {
            brushType: 'stroke'
          },
          hoverAnimation: true
        })
      }
      // 渲染地图
      this.mapChart.setOption(this.option)
    }
  },
  mounted () {
    // 加载地图
    this.mapEchartsInit()
    this.$common.winResize(this.mapChart)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .map-container
    position: relative
    width: 100%
    height: 100%
    .topMenu
      position: absolute
      z-index: 9
      top: 10px
      left: 0
      li
        float: left
        margin-left: 10px
      li.goBack
        cursor: pointer
    .upLevelBtn
      left: 100px
      background: none
      color: #b0d0f1
      cursor: pointer
    .map
      position: absolute
      top: 0
      bottom: 0
      left: 0
      right: 0
</style>
<style>
.tag-box {
  position: relative;
  background: rgba(9, 26, 79, .7);
  padding: 20px;
  box-shadow: 0 0 30px rgba(69, 120, 192, .6) inset;
}
.tag-box:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 20px;
  box-sizing: border-box;
  border: solid #1e82c7;
  border-width: 1px 1px 0 1px;
}
.tag-box:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20px;
  box-sizing: border-box;
  border: solid #1e82c7;
  border-width: 0 1px 1px 1px;
}
.tag-box h3 i{
  margin-right: 5px;
}
.tag-box li {
  width: 160px;
  color: #40b7e6;
  padding-top: 10px;
}
.tag-box li.warn {
  color: #ffe98f;
}
.tag-box li i {
  float: right;
}
</style>



