<template>
  <div class="summary" :class="data === '暂无总结' ? 'hide' : ''" v-html="data">
  </div>
</template>

<script>
export default {
  name: 'Summary',
  props: {
    'data': {
      type: String,
      default: '暂无总结'
    }
  },
  methods: {

  }
}
</script>

<style lang="stylus" scoped>
.summary
  border-radius 4px
  background rgba(0, 0, 0, 0.2)
  border: 1px solid rgba(255, 255, 255, 0.05)
  box-shadow inset 0 0 4px rgba(0, 0, 0, 0.1)
  height: 80px
  line-height: 20px
  overflow-x: hidden
  padding: 10px
  box-sizing: border-box
  margin-top: 5px
  color: #546cb3
  &.hide
    opacity: 0
</style>



