<template>
  <div class="e-checkbox">
    <el-checkbox-group v-model="checkboxGroup" :min="1" @change="change">
      <el-checkbox-button
        v-for="value in checkboxVal"
        :label="value"
        :key="value"
      >
        {{value}}
      </el-checkbox-button>
    </el-checkbox-group>
  </div>
</template>

<script>
export default {
  name: 'ElCheckbox',
  props: {
    tabList: {
      type: Array
    }
  },
  data () {
    return {
      checkboxGroup: this.tabList.slice(0, 3),
      checkboxVal: this.tabList
    }
  },
  methods: {
    change (value) {
      this.$emit('getCheckBox', value)
    }
  }
}
</script>

<style>
.e-checkbox {
  max-height: 116px;
  overflow-x: hidden;
}
.e-checkbox .el-checkbox-button {
  margin: 20px 20px 0 0
}
.e-checkbox .el-checkbox-button__inner {
  border-radius: 3px!important;
  border: none!important;
  background: none;
  box-shadow: 0 0 2px #4c95ff inset;
  color: #4dd5ff;
  padding: 10px 14px;
}
.e-checkbox .el-checkbox-button:first-child .el-checkbox-button__inner {
  box-shadow: 0 0 2px #4c95ff inset!important;
}
.e-checkbox .el-checkbox-button.is-checked .el-checkbox-button__inner {
  position: relative;
  background: linear-gradient(to right, #18f8c4 0%, #1847c4 100%);
  text-shadow: 0 1px 3px rgba(0, 0, 0, .5);
}
.e-checkbox .el-checkbox-button.is-checked .el-checkbox-button__inner:before {
  position: absolute;
  top: 3px;
  right: 3px;
  content: '';
  width: 5px;
  height: 5px;
  border:solid #b5ffff;
  border-width: 1px 1px 0 0;
  border-top-right-radius: 2px;
}
</style>



